/** ------------POLICES---------------- */

@font-face {
  font-family: "comfortaa";
  src: url("../fonts/Comfortaa-Light.ttf") format("truetype");
}

@font-face {
  font-family: "Titan One";
  src: url("../fonts/TitanOne-Regular.ttf") format("truetype");
}

/** ------------COULEURS---------------- */

html {
  --color-light: #f3f3f3;
  --color-dark: #000;
  --color-grey: #e4e0de;
  --color-grey-dark: #6d767e;
  --color-grey-light: #f1eee8;
  --color-primary: #8bba66;
  --color-red: #c91818;
  --icone-grey-dark: invert(46%) sepia(15%) saturate(262%) hue-rotate(166deg)
    brightness(94%) contrast(90%);
  --icone-light: invert(100%) sepia(6%) saturate(253%) hue-rotate(286deg)
    brightness(111%) contrast(91%);
  --icone-primary: invert(68%) sepia(23%) saturate(729%) hue-rotate(50deg)
    brightness(98%) contrast(84%);
  --icone-grey: invert(97%) sepia(3%) saturate(208%) hue-rotate(336deg)
    brightness(93%) contrast(94%);
}

.rouge {
  color: var(--color-red);
}

/** ------------BASES---------------- */

body {
  margin: 0;
  height: 100vh;
  background-color: var(--color-grey-light);
  font-family: "comfortaa";
}

#root {
  height: 100%;
}

button {
  border: none;
  margin: 0;
  padding: 0;
  background: none;
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
}

h1,
h2,
p {
  font-weight: inherit;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  width: 100%;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

li {
  z-index: 3;
  position: relative;
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
}

nav,
header {
  z-index: 10;
}

*:focus {
  outline: none;
}

.bouton {
  width: 165px;
  height: 40px;
  background: var(--color-primary);
  border: 1px solid #6d767e;
  border-radius: 25px;
  font-family: "Titan One";
  font-size: 17px;
  letter-spacing: 0.5px;
  color: var(--color-light);
  box-shadow: 1px 2px 3px rgb(0 0 0 / 25%);
}

.bouton:active {
  background: var(--color-dark);
  box-shadow: none;
}
.bouton.clic-rouge:active {
  background: var(--color-red);
  box-shadow: none;
}

input[type="checkbox"] {
  position: relative;
  height: 24px;
  width: 24px;
  border: 3px solid var(--color-grey-dark);
  appearance: none;
  border-radius: 5px;
}

input[type="checkbox"]:checked::before {
  content: "";
  background-size: 100% !important;
  filter: var(--icone-grey-dark);
  background: url(../images/check.svg);
  width: 15px;
  height: 15px;
  display: block;
  position: absolute;
  top: 2px;
  left: 2px;
}

.barre {
  text-decoration-line: line-through;
}

/** ------------HEADER---------------- */

header {
  position: fixed;
  width: 100vw;
  background: var(--color-grey-light);
}

header.transparent {
  background: none;
}

.header-container {
  height: 35px;
  padding: 20px;
  display: flex;
  justify-content: space-between;
}

header button,
header a {
  width: 33px;
  height: 33px;
  background: var(--color-light);
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 3px 4px 4px rgb(0 0 0 / 25%);
}

header .titre {
  display: flex;
  align-items: center;
  font-family: "Titan One";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  color: var(--color-grey-light);
  text-shadow: 2px 2px 0.5px var(--color-grey-dark),
    0px 2px 0.5px var(--color-grey-dark), -1px 1.5px 0px var(--color-grey-dark),
    -1px -1px 0px var(--color-grey-dark), 1.5px -1px 0 var(--color-grey-dark),
    2px 0px 0 var(--color-grey-dark);
}

header .titre.left-empty {
  margin-left: 10px;
}

header .boutons {
  display: flex;
}

header .boutons a,
header .boutons button {
  margin-right: 11px;
}

header .boutons a:last-child {
  margin-right: 0;
}

header .boutons.vide {
  display: none;
}

/** ------------NAVBAR---------------- */

nav {
  position: fixed;
  width: 100vw;
  bottom: 5px;
}

nav ul {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  margin: 15px 10px;
}

nav ul::before,
nav ul::after {
  content: "";
  width: calc(50% - 59px);
  height: 45px;
  background: var(--color-light);
  position: absolute;
  top: 15px;
  border: 1.5px solid var(--color-grey-dark);
  border-radius: 22px;
}

nav ul::after {
  right: 21px;
}

nav::before {
  content: "";
  position: absolute;
  width: 112px;
  height: 53px;
  top: 13px;
  left: calc(50% - 57px);
  background: url(../images/footer.svg);
  background-size: 100%;
  z-index: 2;
}

li.recette,
li.ingredient {
  position: absolute;
  bottom: 39px;
  left: calc(50% - 25px);
  padding: 12px;
  background: var(--color-primary);
  border-radius: 25px;
  z-index: 10;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.25);
}

li.dashboard,
li.courses {
  padding: 2px 6vw;
}

li.ingredients {
  padding-right: 100px;
  padding-top: 2px;
  padding-bottom: 2px;
}

li.agenda {
  padding: 2px 0;
}

li.dashboard .active div::before,
li.ingredients .active div::before,
li.agenda .active div::before,
li.courses .active div::before {
  content: "";
  background: var(--color-grey);
  width: 39px;
  height: 39px;
  display: block;
  position: absolute;
  top: -8px;
  left: -8px;
  border-radius: 25px;
  filter: opacity(0.2);
}

li.ingredients .active div::before {
  left: -8px;
}

li.ingredient {
  width: 24px;
  height: 24px;
}

.plus.ingredient {
  color: var(--color-light);
  font-size: 33px;
  font-weight: bold;
}

/** ------------PRESENTATION---------------- */

#presentation-dashboard,
#presentation-ingredients,
#presentation-choix-courses {
  display: none;
}

#presentation-dashboard.show,
#presentation-ingredients.show,
#presentation-choix-courses.show {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#presentation-dashboard .logo {
  background: url(../images/logo_app.png);
  height: 25vh;
  width: 32vh;
  background-size: 32vh;
}

#presentation-dashboard .bienvenue {
  font-size: 20px;
  position: absolute;
  top: 42vh;
}

#presentation-dashboard .ingredients,
#presentation-dashboard .recette {
  width: 220px;
  font-size: 15px;
  position: absolute;
}

#presentation-dashboard .ingredients {
  bottom: 255px;
  left: calc(36vw - 110px);
}

#presentation-dashboard .recette {
  bottom: 180px;
  right: calc(48vw - 145px);
  text-align: right;
}

#presentation-dashboard .ingredients::before {
  content: "";
  background: url(../images/fleche1.svg);
  width: 34px;
  height: 170px;
  display: block;
  position: absolute;
  top: 41px;
  left: 48px;
}

#presentation-dashboard .recette::before {
  content: "";
  background: url(../images/fleche2.svg);
  width: 55px;
  height: 83px;
  display: block;
  position: absolute;
  top: 43px;
  left: 100px;
}

#presentation-ingredients .illustration-ingredients,
#presentation-choix-courses .illustration-produits {
  background: url(../images/illustration_ingredients.svg);
  height: 35vh;
  width: 23vh;
  background-size: 23vh;
  margin-top: 4vh;
}

#presentation-ingredients .explications {
  text-align: center;
  width: 240px;
  position: absolute;
  bottom: 220px;
  line-height: 20px;
}

#presentation-ingredients .explications::before,
#presentation-choix-courses .explications::before {
  content: "";
  background: url(../images/fleche3.svg);
  width: 13px;
  height: 105px;
  display: block;
  position: absolute;
  top: 70px;
  left: 112.5px;
}

#presentation-choix-courses .explications::before {
  top: 120%;
}

#presentation-choix-courses .illustration-produits {
  background: url(../images/illustration_produits.svg);
  background-size: 23vh;
}

#presentation-choix-courses .explications {
  text-align: center;
  width: 240px;
  position: absolute;
  bottom: 240px;
  line-height: 20px;
}

/** ------------FILTRES---------------- */

#filtres-dashboard {
  background: var(--color-grey-light);
  display: none;
  justify-content: center;
}

#filtres-dashboard.show {
  display: flex;
  position: sticky;
}

#filtres-dashboard ul {
  overflow: auto;
  white-space: nowrap;
}

#filtres-dashboard li {
  height: 60px;
  width: 60px;
  background: var(--color-light);
  box-shadow: 0px 0px 0px 1px var(--color-grey-dark);
  border-radius: 15px;
  margin: 10px;
  display: inline-block;
}

#filtres-dashboard li.actif {
  background: var(--color-primary);
}

#filtres-dashboard ::-webkit-scrollbar {
  display: none;
}

.icone.filtre {
  position: absolute;
  width: 45px !important;
  height: 45px !important;
  left: 13%;
}

.actif .icone.filtre {
  filter: var(--icone-light) !important;
}

.actif .titre-filtre {
  color: var(--color-light) !important;
}

#filtres-dashboard .titre-filtre {
  position: absolute;
  text-align: center;
  width: 100%;
  top: 72%;
  font-size: 9px;
  font-weight: bold;
  color: var(--color-grey-dark);
}

/** ------------DASHBOARD---------------- */

.dashboard-container {
  padding-top: 75px;
  overflow: hidden;
  height: calc(100vh - 75px);
  display: flex;
  flex-direction: column;
}

#vignettes-dashboard {
  overflow-y: auto;
  height: calc(100vh - 162px);
}

#vignettes-dashboard ul {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

#vignettes-dashboard .vignette {
  width: 85vw;
  height: 155px;
  margin: 10px 0px;
  background: var(--color-light);
  box-shadow: 0px 0px 0px 1px var(--color-grey-dark);
  border-radius: 17px 0px;
  max-width: 380px;
  overflow: hidden;
}

#vignettes-dashboard .vignette:last-child {
  margin-bottom: 110px;
}

#vignettes-dashboard .badge-temps {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 25px;
  max-width: 85px;
  font-size: 12px;
  border-radius: 20px;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.25);
  background: var(--color-grey-light);
  position: absolute;
  right: 12px;
  top: 10px;
}

#vignettes-dashboard .badge-temps .duree {
  margin-right: 9px;
  font-weight: bold;
  color: var(--color-dark);
  margin-top: 1px;
}

#vignettes-dashboard .vignette .photo {
  background: var(--color-primary);
  width: 85vw;
  max-width: 380px;
  height: 99px;
  border-radius: 16px 0 0 0;
  box-shadow: 0px 1px 0px 0px var(--color-grey-dark);
}

#vignettes-dashboard .vignette .photo img {
  width: 115%;
  height: 100%;
  margin-left: -15px;
  object-fit: cover;
}

#vignettes-dashboard .vignette .infos {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 45px;
  padding: 5px 10px;
  color: var(--color-grey-dark);
  font-size: 12px;
  font-weight: bold;
}

#vignettes-dashboard .vignette .titre {
  font-size: 15px;
  color: var(--color-dark);
}

#vignettes-dashboard .vignette .calories,
#vignettes-dashboard .vignette .prix {
  margin-top: 8px;
}

#vignettes-dashboard .vignette .info-personnes {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.dashboard-container .no-recette {
  text-align: center;
  margin-top: 50px;
}

/** ----------------ONGLETS---------------- */

.tab-list {
  display: flex;
  width: 100vw;
  height: 29px;
  justify-content: space-around;
  background: var(--color-grey-light);
  border-bottom: 1px solid var(--color-grey-dark);
  z-index: 3;
  position: relative;
}

.tab-item {
  width: 50%;
  text-align: center;
  color: var(--color-grey-dark);
}

.tab-item::before {
  content: "";
}

.tab-item.active {
  color: var(--color-dark);
}

.tab-item.active::before {
  content: "";
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;
  bottom: -5px;
  border-bottom: 5px solid var(--color-primary);
}

.tab-item div {
  font-size: 13px;
  font-weight: bold;
  margin-top: 7px;
  margin-bottom: 5px;
}

.tab-content h2 {
  font-weight: bold;
  letter-spacing: 1px;
  font-size: 14px;
  padding: 10px 15px;
  background: var(--color-grey);
}

/** ------------DETAIL-RECETTE---------------- */

.detail-container {
  height: 100%;
  overflow: hidden;
}

.container-recette-detail {
  height: calc(100vh - 393px);
  overflow-y: auto;
  overflow-x: hidden;
}

.detail-container .photo-container img {
  width: 115%;
  height: 100%;
  margin-left: -15px;
  object-fit: cover;
}

.detail-container .photo-container {
  width: 100vw;
  height: calc(8vw + 190px);
  background: var(--color-primary);
  position: relative;
}

.detail-container .photo-container::after {
  content: "";
  bottom: 0;
  position: absolute;
  border-bottom: calc(8vw + 17px) solid var(--color-grey-light);
  border-right: 100vw solid transparent;
}

.detail-container .titre {
  position: absolute;
  top: 155px;
  right: 0;
  background: var(--color-grey);
  border-radius: 20px;
  padding: 3px 10px;
  margin-right: 10px;
  font-family: "Titan One";
  font-size: 16px;
}

.detail-container .badge-saison {
  position: absolute;
  top: 164px;
  left: 25px;
  background: var(--color-light);
  border-radius: 20px;
  border: 1px solid var(--color-grey-dark);
  padding: 3px;
}

.detail-container .info-recette-container {
  display: flex;
  justify-content: space-evenly;
  transform: rotate(7.5deg);
  width: 102vw;
  padding-bottom: 5px;
  background: var(--color-grey-light);
  z-index: 4;
  position: relative;
}

.detail-container .icone-container {
  background: var(--color-light);
  border-radius: 20px;
  padding: 10px;
  width: 20px;
  height: 20px;
  margin: 3.5px;
}

.detail-container .info-recette-container .info-temps,
.detail-container .info-recette-container .info-personnes,
.detail-container .info-recette-container .info-argent,
.detail-container .info-recette-container .info-calories {
  background: var(--color-primary);
  height: 80px;
  width: 46px;
  border-radius: 25px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  transform: rotate(-7.5deg);
}

.detail-container .info-recette-container .valeur {
  font-family: "Titan One";
  font-size: 13px;
  text-align: center;
}

.detail-container .info-recette-container .unite {
  font-size: 10px;
  text-align: center;
  margin-bottom: 6px;
}

.detail-container .tab-list {
  width: 103vw;
  transform: rotate(7.5deg);
  margin-left: -1vw;
  height: inherit;
}

.detail-container .tab-content .swipe {
  position: relative;
  margin-top: 50px;
}

.detail-container .tab-content .swipe::before {
  content: "";
  position: absolute;
  top: -50px;
  border-bottom: 50px solid var(--color-grey);
  border-right: 100vw solid transparent;
}

.detail-container .tab-content {
  margin-top: -43px;
}

.detail-container .tab-item div {
  margin-top: 24px;
}

.detail-container .tab-content .etapes {
  margin: 20px 35px;
}

.detail-container .tab-content .ingredients {
  margin: 20px 43px;
}

.detail-container .tab-content .ingredients li,
.detail-container .tab-content .etapes li {
  margin-top: 10px;
  font-size: 15px;
  display: flex;
}

.detail-container .tab-content .etapes li::before {
  content: ".";
  color: var(--color-primary);
  font-size: 60px;
  position: absolute;
  top: -42px;
  left: -20px;
}

.detail-container .tab-content .ingredients li span {
  color: var(--color-primary);
  font-weight: bold;
  font-size: 12px;
  margin-left: 10px;
  display: flex;
  align-items: center;
  margin-top: 1.5px;
}

.detail-container .tab-content .etapes p {
  font-size: 13px;
}

footer {
  font-size: 12px;
  color: var(--color-grey-dark);
  margin: 15px;
  margin-top: 50px;
}

/** ------------INGREDIENTS---------------- */

.ingredients-container {
  padding-top: 75px;
  overflow-x: hidden;
  overflow-y: auto;
  height: calc(100vh - 75px);
}

#vignettes-ingredients ul {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

#vignettes-ingredients .vignette {
  width: 85vw;
  height: 50px;
  margin: 10px 0px;
  background: var(--color-light);
  box-shadow: 0px 0px 0px 1px var(--color-grey-dark);
  border-radius: 17px 0px;
  transition: 0.2s;
}

#vignettes-ingredients .vignette.swiped {
  margin-right: 90px;
}

#vignettes-ingredients .vignette .infos {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 45px;
  padding: 5px 10px;
  color: var(--color-grey-dark);
  font-size: 11px;
  font-weight: bold;
}

#vignettes-ingredients .vignette .titre {
  font-size: 15px;
  color: var(--color-dark);
}

#vignettes-ingredients .vignette .calories {
  margin-top: 8px;
}

#vignettes-ingredients .vignette .infos:first-child {
  margin-left: 55px;
}

#vignettes-ingredients ul li:last-child:not(li li) {
  margin-bottom: 110px;
}

#vignettes-ingredients .vignette:last-of-type {
  margin-bottom: 20px;
}

#vignettes-ingredients li h1 {
  font-size: 15px;
  color: var(--color-grey-dark);
  text-align: center;
  margin-top: 10px;
  margin-bottom: 0;
}

/** ------------FORMULAIRES---------------- */

form .container {
  display: flex;
  flex-direction: column;
  padding: 3vw 5vw;
}

form .select-menu {
  position: relative;
}

form .select-menu i {
  background: url(../images/chevron.svg);
  width: 24px;
  height: 24px;
  filter: var(--icone-grey-dark);
  display: block;
  position: absolute;
  background-size: 100% !important;
  right: 8px;
  top: 6px;
  transform: rotate(-90deg);
  transition: 0.5s;
}

form .select-menu.active i {
  transform: rotate(90deg);
}

form .options {
  background: var(--color-light);
  position: absolute;
  top: 38px;
  width: 100%;
  box-shadow: 0px 0px 0px 1.5px var(--color-grey-dark);
  border-radius: 6px;
  transition: opacity 0.1s 0.2s, max-height 0.3s ease-out;
  max-height: 0;
  opacity: 0;
  overflow-y: auto;
  z-index: 9;
}

form .select-menu.active .options {
  max-height: 185px;
  opacity: 1;
  transition: opacity 0.2s, max-height 0.3s ease-out;
}

form .options ul {
  opacity: 0;
  transition: 0.3s;
}

form .select-menu.active .options ul {
  opacity: 1;
  transition: 0.3s ease-out 0.3s;
}

form .options li {
  padding: 10px 12px;
  color: var(--color-grey-dark);
  font-size: 15px;
  font-weight: bold;
}

form .options li:hover {
  background: var(--color-grey);
}

form label,
form h1 {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 15px;
}

form .saisie,
form .ajout-photo-container {
  height: 30px;
  width: calc(100% - 27px);
  border: 1.5px solid var(--color-grey-dark);
  border-radius: 6px;
  padding: 1px 12px;
  background: var(--color-light);
  font-family: "comfortaa";
  font-size: 12px;
  font-weight: bold;
  letter-spacing: 1px;
  color: var(--color-dark);
}

form textarea.saisie {
  max-width: 73vw;
  padding-top: 15px;
}

form .saisie::placeholder {
  font-family: "comfortaa";
  font-size: 11px;
  font-weight: bold;
  letter-spacing: 1px;
}

form .error .saisie {
  box-shadow: 0px 0px 0px 1px var(--color-red);
}

form .erreur {
  font-size: 12px;
  color: var(--color-red);
  margin: 5px;
  display: none;
}

form .error .erreur {
  display: block;
}

.validation-container {
  display: flex;
  justify-content: center;
  margin: calc(18vh - 110px);
}

.bouton-ajout {
  display: flex;
  justify-content: center;
  width: 100%;
  font-size: 12px;
  font-family: "comfortaa";
  color: var(--color-grey-dark);
  font-weight: bold;
  margin-top: 20px;
}

/** ------------FORMULAIRE ICONES -------------------- */

li.icone {
  padding-left: 50px !important;
}

li.icone .icone {
  width: 24px;
  height: 24px;
  position: absolute;
  left: 14px;
  top: 6px;
}

/** ------------FORMULAIRE INGREDIENT---------------- */

.ingredient-container {
  padding-top: 75px;
  overflow-x: hidden;
  overflow-y: auto;
  height: calc(100vh - 75px);
}

#formulaire-ingredient {
  display: flex;
  flex-direction: column;
}

#saisieCalories div:first-of-type,
#saisiePrix div:first-of-type,
#saisiePoids div:first-of-type {
  display: flex;
  align-items: center;
}

#saisiePrix_input,
#saisieCalories_input,
#saisiePoids_input {
  width: 35px !important;
  text-align: center;
}

#saisieCalories_input + div,
#saisiePrix_input + div,
#saisiePoids_input + div {
  color: var(--color-grey-dark);
  font-size: 12px;
  margin-left: 15px;
  font-weight: bold;
}

/** ------------FORMULAIRE RECETTE---------------- */

.recette-container {
  padding-top: 75px;
  overflow-x: hidden;
  overflow-y: auto;
  height: calc(100vh - 75px);
}

#saisiePreparation div:first-of-type {
  display: flex;
  justify-content: space-between;
}

#saisiePreparation_input {
  width: 50vw;
}

#saisiePreparation .select-menu {
  width: 30vw;
}

#saisiePersonnes_input {
  width: 15px;
  margin: 0 10px;
}

#saisiePersonnes {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}

#saisiePersonnes div {
  color: var(--color-grey-dark);
  font-size: 12px;
  font-weight: bold;
}

.saisie-ingredients-container {
  display: none;
}

.saisie-ingredients-container.show {
  display: block;
}

.ajout_ingredient_item,
.ajout_instruction_item {
  display: flex;
  margin-bottom: 15px;
  align-items: center;
  justify-content: space-between;
}

.ajout_ingredient_item div {
  color: var(--color-grey-dark);
  font-size: 12px;
  font-weight: bold;
}

.ajout_ingredient_item .select-menu {
  width: 50vw;
}

.ajout_ingredient_item > input {
  width: 18vw;
}

#saisiePhoto .ajout-photo-container {
  height: 115px;
  overflow: hidden;
  padding: 0 12px;
}

#saisiePhoto .container-bouton {
  height: 115px;
  display: none;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

#saisiePhoto .container-bouton.show {
  display: flex;
}

#saisiePhoto .text-bouton {
  color: var(--color-grey-dark);
  margin-top: 7px;
}

input[type="file"] {
  display: none;
}

.ajout-photo-container img {
  width: 115%;
  height: 100%;
  margin-left: -15px;
  object-fit: cover;
  display: none;
}

.ajout-photo-container img.show {
  display: block;
}

/** ------------AGENDA---------------- */

.agenda-container {
  padding-top: 75px;
}

.container-selection-semaine {
  display: flex;
  width: 50vw;
  justify-content: space-between;
  margin-left: 3vw;
}

.header-agenda {
  background: var(--color-grey);
}

.agenda-container .header-agenda {
  display: flex;
  height: 45px;
  justify-content: center;
  align-items: center;
}

.container-selection-semaine h2 {
  color: var(--color-grey-dark);
  padding: 10px 0;
}

.container-dates {
  height: calc(100vh - 393px);
  overflow-y: auto;
  overflow-x: hidden;
}

.agenda-container .container-dates {
  height: calc(100vh - 150px);
}

.container-jour {
  margin-top: 12px;
  margin-right: 23px;
  margin-left: 23px;
}

.container-jour div:first-of-type {
  display: flex;
  justify-content: space-between;
}

.container-jour h3 {
  margin: 0;
  font-size: 15px;
  display: flex;
  align-items: center;
}

.container-jour h3 span {
  font-size: 12px;
  color: var(--color-grey-dark);
  margin-left: 10px;
  margin-top: 1.5px;
}

.container-jour .aujourdhui {
  font-size: 10px;
  margin-left: 10px;
  display: flex;
  align-items: center;
  color: var(--color-primary);
  font-weight: bold;
}

.container-jour .info {
  width: 100%;
}

.container-jour button.ajouter {
  font-size: 32px;
  font-family: "comfortaa";
  color: var(--color-primary);
  font-weight: bold;
}

.container-jour ul {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.vignette-recette {
  display: flex;
  align-items: center;
  width: 85vw;
  height: 50px;
  margin: 5px 0px;
  background: var(--color-light);
  box-shadow: 0px 0px 0px 1px var(--color-grey-dark);
  border-radius: 17px 0px;
  transition: 0.2s;
}

.vignette-recette.swiped {
  margin-right: 90px;
}

.vignette-recette .titre-recette {
  font-size: 15px;
  font-weight: bold;
  color: var(--color-dark);
}

.vignette-recette .container-infos {
  width: 100%;
}

.vignette-recette .prix-cal {
  display: flex;
  font-size: 12px;
  font-weight: bold;
  color: var(--color-grey-dark);
  justify-content: space-between;
  margin-top: 6px;
  margin-right: 10px;
}

.vignette-recette .image-recette {
  width: 80px;
  height: 40px;
  border-radius: 10px;
  background: var(--color-primary);
  margin: 0 7px;
  overflow: hidden;
}

.vignette-recette .image-recette img {
  width: 120%;
  height: 100%;
  margin-left: -10px;
  object-fit: cover;
}

.container-jour:last-of-type {
  margin-bottom: 30px;
}

.agenda-container .container-jour:last-of-type {
  margin-bottom: 110px;
}

.icone.recette.suppr {
  opacity: 0;
  position: absolute;
  top: 12px;
  right: -50px;
  transition: 1s;
  filter: var(--icone-primary);
}

.swiped .icone.recette.suppr {
  opacity: 1;
  transition: 0s;
}

.agenda-mois .container-vignette {
  height: 70px;
  width: 70px;
  background: var(--color-light);
  border-radius: 10px 0px;
  box-shadow: 0px 0px 0px 1px var(--color-grey-dark);
  margin: 10px;
}

.agenda-mois .container-vignette.aujourdhui {
  background: var(--color-primary);
}

.agenda-mois .container-vignette.aujourdhui ul li,
.agenda-mois .container-vignette.aujourdhui h4 {
  color: var(--color-light);
}

.agenda-mois .container-mois {
  height: calc(100vh - 150px);
  overflow-y: auto;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.agenda-mois .container-semaine {
  max-width: 95vw;
}

.agenda-mois .container-semaine:last-of-type {
  margin-bottom: 100px;
}

.agenda-mois h3 {
  font-size: 12px;
  font-weight: bold;
  color: var(--color-grey-dark);
  margin-top: 20px;
  text-align: center;
}

.agenda-mois h4 {
  font-size: 10px;
  font-weight: bold;
  color: var(--color-grey-dark);
  margin: 0;
  padding-top: 4px;
  text-align: center;
}

.agenda-mois ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.agenda-mois .container-vignette ul {
  flex-direction: column;
  height: 50px;
  padding-top: 3px;
}

.agenda-mois .container-vignette li {
  font-size: 9px;
  color: var(--color-primary);
  text-align: center;
  font-weight: bold;
}

.agenda-mois .container-vignette li:nth-child(3) {
  font-size: 12px;
  letter-spacing: 2px;
}

.icone-pomme-ajout {
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color-grey);
  font-weight: bold;
  font-size: 20px;
  margin-top: 5px;
  margin-left: -1px;
  position: relative;
}

.icone-pomme-ajout::before {
  content: "";
  background-size: 100% !important;
  position: absolute;
  filter: var(--icone-grey);
  background: url(../images/apple.svg);
  width: 45px;
  height: 45px;
  top: -1px;
  left: 14px;
}

/** -------------RECHERCHE------------- */

#container_recherche {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 25px;
  height: 35px;
  box-shadow: inset 1px 1px 1px rgb(0 0 0 / 25%);
  border-radius: 20px;
  background: var(--color-grey);
}

#container_recherche input {
  width: calc(100% - 93px);
  padding: 0;
  border: none;
  font-family: "comfortaa";
  font-size: 15px;
  font-weight: bold;
  background: none;
  color: var(--color-dark);
}

#container_recherche .croix {
  font-family: "comfortaa";
  font-size: 40px;
  transform: rotate(45deg);
  margin-right: 10px;
  margin-left: 10px;
  margin-top: 3px;
  color: var(--color-grey-dark);
  opacity: 0;
}

#container_recherche .croix.show {
  opacity: 1;
}

#container_recherche .icone.loupe {
  margin: 0px 8px;
  margin-top: 1px;
}

#container_recherche input::placeholder {
  color: var(--color-grey-dark);
  font-weight: initial;
}

/** ------------COURSES---------------- */

.courses-container {
  padding-top: 75px;
}

.courses-container .header-courses {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 35px;
  background: var(--color-grey);
  border-bottom: 1px solid var(--color-light);
}

.courses-container .header-courses .info {
  color: var(--color-grey-dark);
  margin-left: 15px;
  margin-top: 2px;
}

.courses-container .select-menu {
  position: relative;
  margin-right: 5px;
}

.courses-container h2 {
  font-size: 15px;
  height: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--color-grey);
  color: var(--color-grey-dark);
}

.courses-container .select-menu i {
  background: url(../images/chevron.svg);
  width: 24px;
  height: 24px;
  filter: var(--icone-grey-dark);
  display: block;
  position: absolute;
  background-size: 100% !important;
  right: 8px;
  top: 6px;
  transform: rotate(-90deg);
  transition: 0.5s;
}

.courses-container .select-menu.active i {
  transform: rotate(90deg);
}

.courses-container .options {
  background: var(--color-light);
  position: absolute;
  top: 38px;
  width: 100%;
  box-shadow: 0px 0px 0px 1.5px var(--color-grey-dark);
  border-radius: 6px;
  transition: opacity 0.1s 0.2s, max-height 0.3s ease-out;
  max-height: 0;
  opacity: 0;
  overflow-y: auto;
  z-index: 9;
}

.courses-container .select-menu.active .options {
  max-height: 185px;
  opacity: 1;
  transition: opacity 0.2s, max-height 0.3s ease-out;
}

.courses-container .options ul {
  opacity: 0;
  transition: 0.3s;
}

.courses-container .options li {
  padding: 10px 12px;
  color: var(--color-grey-dark);
  font-size: 15px;
  font-weight: bold;
}

.courses-container .select-menu.active .options ul {
  opacity: 1;
  transition: 0.3s ease-out 0.3s;
}

.courses-container .saisie {
  height: 30px;
  width: 135px;
  border: none;
  padding: 1px 12px;
  margin-top: 2px;
  background: var(--color-grey);
  font-family: "comfortaa";
  font-size: 15px;
  letter-spacing: 1px;
  color: var(--color-grey-dark);
}

.container-check-liste {
  background: var(--color-grey);
  height: calc(100vh - 111px);
  overflow-y: auto;
  overflow-x: hidden;
}

.container-check-liste ul li:not(:last-of-type) ul {
  background: var(--color-grey-light);
}

.container-check-liste .liste-finie {
  margin-bottom: 150px;
}

.container-check-liste ul li ul li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px;
}

.container-check-liste ul li ul li div {
  font-size: 15px;
  font-weight: bold;
  color: var(--color-dark);
}

.container-check-liste ul li ul li div span {
  font-size: 15px;
  font-weight: initial;
  color: var(--color-primary);
  margin-left: 10px;
}

.no-courses {
  text-align: center;
  margin-top: 40px;
  color: var(--color-dark);
}

/** ------------CHOIX-PLAT---------------- */

.choix-plat-container {
  padding-top: 75px;
}

.choix-plat-container h2 {
  letter-spacing: 1px;
  font-size: 14px;
  padding: 10px 15px;
  background: var(--color-grey);
  text-align: center;
  color: var(--color-grey-dark);
}

.choix-plat-container ul {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.choix-plat-container .no-recette {
  text-align: center;
}

/** ------------CHOIX COURSES---------------- */

.choix-courses-container {
  padding-top: 75px;
  overflow-x: hidden;
  overflow-y: auto;
  height: calc(100vh - 75px);
}

#vignettes-produits {
  margin-bottom: 1vh;
  max-height: calc(55vh - 110px);
  overflow-y: auto;
  overflow-x: hidden;
}

#vignettes-produits ul {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

#vignettes-produits .vignette {
  width: 85vw;
  height: 50px;
  margin: 10px 0px;
  background: var(--color-light);
  box-shadow: 0px 0px 0px 1px var(--color-grey-dark);
  border-radius: 17px 0px;
  transition: 0.2s;
}

#vignettes-produits .vignette.actif {
  background: var(--color-primary);
}

#vignettes-produits .vignette.swiped {
  margin-right: 90px;
}

#vignettes-produits .vignette .infos {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 45px;
  padding: 5px 10px;
  color: var(--color-grey-dark);
  font-size: 11px;
  font-weight: bold;
}

#vignettes-produits button {
  width: 100%;
}

#vignettes-produits .vignette .titre {
  font-size: 15px;
  color: var(--color-dark);
}

#vignettes-produits .vignette.actif .titre {
  color: var(--color-light);
}

#vignettes-produits .vignette .infos:first-child {
  margin-left: 55px;
}

#vignettes-produits .vignette:last-of-type {
  margin-bottom: 20px;
}

#vignettes-produits li h1 {
  font-size: 15px;
  color: var(--color-grey-dark);
  text-align: center;
  margin-top: 10px;
  margin-bottom: 0;
}

.actif .icone.produit {
  filter: var(--icone-light);
}

#container-choix-courses,
#formulaire-choix-courses {
  display: none;
}

#container-choix-courses.show,
#formulaire-choix-courses.show {
  display: flex;
  flex-direction: column;
}

.choix-courses-container .explication-suppression {
  margin: 20px;
  text-align: center;
}

.choix-courses-container no-produit {
  text-align: center;
}

/** ------------AJOUT PRODUIT---------------- */

.ajout-produit-container {
  padding-top: 75px;
  overflow-x: hidden;
  overflow-y: auto;
  height: calc(100vh - 75px);
}

#formulaire-produit {
  height: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.ajout-produit-container .validation-container {
  margin: 0;
  display: flex;
  height: 40%;
  align-items: center;
}

/** ------------PARAMETRES---------------- */

.parametres-container {
  padding-top: 75px;
  height: 100%;
}

.parametres-container div {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  margin: 50px 0;
}

.parametres-container .bouton {
  margin-bottom: 30px;
}

/** ------------ICONES---------------- */

.icone {
  background-size: 100% !important;
  filter: var(--icone-grey-dark);
  position: relative;
}

.icone.setting {
  background: url(../images/setting.svg);
  width: 24px;
  height: 24px;
  margin-left: 1px;
}

.icone.loupe {
  background: url(../images/loupe.svg);
  width: 24px;
  height: 24px;
}

.icone.back {
  background: url(../images/chevron.svg);
  width: 24px;
  height: 24px;
  margin-right: 1px;
}

.icone.next {
  background: url(../images/chevron.svg);
  width: 24px;
  height: 24px;
  margin-right: 1px;
