  left: 27%;
}

.icone.hiver {
  background: url(../images/snow.svg);
  width: 20px;
  height: 20px;
}

.icone.filtre.hiver {
  width: 27px !important;
  height: 27px !important;
  top: 13%;
  left: 28%;
}

.icone.ete {
  background: url(../images/sun.svg);
  width: 20px;
  height: 20px;
}

.icone.filtre.ete {
  width: 27px !important;
  height: 27px !important;
  top: 13%;
  left: 28%;
}

.icone.printemps {
  background: url(../images/flower.svg);
